*
    box-sizing: border-box

img
    max-width: 100%

body
    padding: 0
    margin: 0
    color: #313747

#root
    background-color: #d3d6e0
    min-height: 100vh
    padding-bottom: 50px

.container
    max-width: 1140px
    margin: auto
    padding: 0 15px

.topbar
    background-color: #302F32
    padding: 0 15px
    color: #fff
    .container
        display: flex
        flex-wrap: wrap
        align-items: center
        justify-content: space-between
    .logo
        padding: 15px 15px 15px 0
        width: 100px
        border-right: 1px solid #464547
    .info
        display: flex
        flex-direction: column
        padding: 15px
    .full-version
        font-family: Bold
        color: #fff
    .nav
        a
            color: #fff
            text-decoration: none
            padding: 10px
            &:hover
                text-decoration: underline
    .user
        display: flex
        align-items: center
    .user__avatar
        width: 51px
        height: 51px
        border-radius: 50%
        margin-right: 10px
        overflow: hidden
    .user__info
        b
            font-size: 16px
            font-family: Bold
        span
            font-size: 14px

.main-nav
    display: flex
    align-items: center
    justify-content: center
    background-color: #302F32
    border-top: 1px solid #464547
    &__item
        color: #fff
        margin: 15px
        border-bottom: 1px solid #302F32
        transition: all 0.3s
        cursor: pointer
        font-family: Bold
        &:hover
            border-bottom: 1px solid #01C158
    &.fixed
        position: fixed
        top: 0
        left: 0
        z-index: 99
        width: 100%

.section
    background-color: #E1E3E7
    border-radius: 8px
    margin: 24px 0
    width: 100%
    .section
        background-color: #F3F5F8
        margin: 12px 0
        .section__header
            background-color: #585858
            font-size: 18px
    &__header
        background-color: #3B3A40
        padding: 15px 20px
        color: #fff
        font-weight: 700
        font-size: 20px
        font-family: Bold
        border-radius: 8px 8px 0 0
        display: flex
        align-items: center
        .icon
            margin-right: 15px
        .collaps-icon
            margin-left: auto
            cursor: pointer
            opacity: 0.7
            transition: all .3s
            &:hover
                opacity: 1
    &__body
        padding: 24px
        display: flex
        flex-wrap: wrap
    &.closed
        .section__header
            border-radius: 8px
        .section__body
            height: 0
            overflow: hidden
            padding: 0
        .collaps-icon
            transform: rotateX(180deg)

.field
    margin-bottom: 22px
    display: block
    position: relative
    .header
        display: flex
        margin-bottom: 10px
        font-weight: 500
        .length
            margin-left: auto
            img
                margin-left: 10px
    .input
        padding: 17px 20px
        font-size: 15px
        border: 0
        border-radius: 8px
        box-sizing: border-box
        width: 100%
        max-width: 100%

input.input:disabled
    border: 1px solid #ccc

.checkbox
    width: 21px
    height: 21px
    position: relative
    &:checked
        background-color: #fff
        &:after
            content: ''
            display: block
            width: 100%
            height: 100%
            left: 0
            top: 0
            background: #fff url('./img/check-icon.png') no-repeat center / 90%

.company-logo
    margin-left: auto

.logo-wrapper
    border-radius: 8px
    background-color: #fff
    position: relative
    &:after
        content: ''
        padding-top: 100%
        display: block
    .ff-notice
        position: absolute
        width: 100%
        height: 100%
        display: flex
        align-items: center
        justify-content: center
        text-align: center
        font-size: 15px

.btn, .upload-btn
    width: 190px
    max-width: 100%
    padding: 15px
    border-radius: 8px
    text-align: center
    color: #333
    font-family: Bold
    margin-top: 12px
    cursor: pointer
    background-color: #fff
    transition: all 0.3s
    &:hover
        background-color: #f3f3f3

.upload-btn
    position: absolute
    color: #fff
    background-color: #01C158
    &:hover
        background-color: #049947

input[type="file"]
    opacity: 0
    width: 1px

.add
    display: flex
    flex-wrap: wrap
    align-items: center
    cursor: pointer
    margin-bottom: 24px
    img
        margin-right: 15px

.remove
    margin: auto
    cursor: pointer
    transform: rotate(45deg)

//================== Optional input
.optional-input
    display: flex
    margin: 15px 0
.oi__left
    margin-right: 15px
    display: flex
    flex-direction: column
    align-items: center
    .switch
        margin-top: 8px

.oi__right
    width: 100%

.switch
    position: relative
    display: inline-block
    width: 42px
    height: 23px
    input
        display: none

.slider
    position: absolute
    cursor: pointer
    top: 0
    left: 0
    right: 0
    bottom: 0
    background-color: #ccc
    -webkit-transition: .4s
    transition: .4s

.slider:before
    position: absolute
    content: ""
    height: 19px
    width: 19px
    left: 2px
    bottom: 2px
    background-color: white
    -webkit-transition: .4s
    transition: .4s

input:checked + .slider
    background-color: #78DC4B

input:focus + .slider
    box-shadow: 0 0 1px #78DC4B

input:checked + .slider:before
    -webkit-transform: translateX(19px)
    -ms-transform: translateX(19px)
    transform: translateX(19px)

/* Rounded sliders */
.slider.round
    border-radius: 34px

.slider.round:before
    border-radius: 50%

//======================= Social icons
.social-icon
    width: 53px
    height: 53px
    border-radius: 50%
    background: #ccc
    &.vk
        background: #6695BD url(./img/vk.svg) no-repeat center / contain
    &.fb
        background: #3B5998 url(./img/fb.svg) no-repeat center / contain
    &.ok
        background: #FF9205 url(./img/ok.svg) no-repeat center / contain
    &.whatsapp
        background: #65BC54 url(./img/WhatsApp.svg) no-repeat center / contain
    &.viber
        background: #7B519D url(./img/Viber.svg) no-repeat center / contain
    &.telegram
        background: #2AA1DA url(./img/Telegram.svg) no-repeat center / contain

.s1__2
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    margin-top: 20px

.delimeter
    border-top: 1px solid #D3D6E0
    width: calc(100% + 50px)
    margin-left: -25px
    margin-right: -25px
    margin-bottom: 20px
    margin-top: 20px

// ============================ NOTICE
.notice
    width: 100%
    padding: 15px 30px
    color: #fff
    background: #FFA412
    border-radius: 8px
    margin: 20px 0
    font-family: Bold
    text-align: center

//============================= BADGE
.badge
    border-radius: 50px
    padding: 5px 15px
    background-color: #333
    font-size: 10px
    font-family: bold
    color: #fff
    &.option
        cursor: pointer

.footer
    .btn,.upload-btn
        position: relative
        margin: 10px

//============================= MEDIA
@media (max-width: 540px)
    .topbar
        .container
            .user
                margin: 15px auto
